import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FormControl, Validators, FormGroup, ValidationErrors} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Helpers } from '../../../../helpers';
import { ScriptLoaderService } from '../../../../_services/script-loader.service';
import { GlobalValidate } from '../../../../_services/validate.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../animations/round-anticlock.animation';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { ToastComponent } from '../../../layouts/toast-notification/toast.component';
import 'rxjs/add/operator/startWith';
import 'rxjs/add/observable/merge';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import 'rxjs/add/observable/fromEvent';
import 'rxjs/add/operator/filter' ;

const NUM_REGEX=/[0-9]/;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./project-add.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    	FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})

export class ProjectAddComponent{
@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
@ViewChild(ToastComponent) toast: ToastComponent;

projectForm: FormGroup;

project = new ProjectData();
status;
projectId=0;
customer=[];
bootstrapFormValues = [];
type=[];
state=[];
Executive=[];
projectstartdateMsg='';
projectdeadlineMsg='';
public customers = [];
public types=[];
public Status=[];
public Executives=[];
msg = [];

	constructor(private _script: ScriptLoaderService, 	private http: HttpClient, private router: Router, private route:ActivatedRoute, private globalValid: GlobalValidate) {
		if(this.projectId==0){
			this.status="Add";
			this.populateDropDownData();
		}else{
		this.status="Update";
			this.populateData();
		}
	}


	ngOnInit() {
			this.projectForm = new FormGroup({			
				'pmTitle' : new FormControl('', [ Validators.required]),
				'customerId' : new FormControl(''),
				'pmPmtypeId' : new FormControl(''),
				'pmBudget' : new FormControl('', [ Validators.required,Validators.pattern(NUM_REGEX)]),
//				'projectStartDate' : new FormControl('',[ Validators.required]),
//				'pmEndDate' : new FormControl('',[ Validators.required]),
				'pmPmstatusId' : new FormControl(''),
				'pmexeEmpIds' : new FormControl(''),
				'pmComplete' : new FormControl(''),
				'pmActive' : new FormControl(''),
				'pmComment' : new FormControl(''),
				'pmNotes' : new FormControl(''),
				
			});
	}

   get pmTitle(){ return this.projectForm.get('pmTitle')};
   get customerId(){ return this.projectForm.get('customerId')};
   get pmPmtypeId(){ return this.projectForm.get('pmPmtypeId')};
   get pmBudget(){ return this.projectForm.get('pmBudget')};
//   get projectStartDate(){ return this.projectForm.get('projectStartDate')};
//   get pmEndDate(){ return this.projectForm.get('pmEndDate')};
   get pmPmstatusId(){ return this.projectForm.get('pmPmstatusId')};
   get pmexeEmpIds(){ return this.projectForm.get('pmexeEmpIds')};
   get pmComplete(){ return this.projectForm.get('pmComplete')};
   get pmActive(){ return this.projectForm.get('pmActive')};
   get pmComment(){ return this.projectForm.get('pmComment')};
   get pmNotes(){ return this.projectForm.get('pmNotes')};
	
ngAfterViewInit(){
		  $.getScript('assets/app/js/bootstrap-material-datetimepicker.js',function(){
		   });
		  
		   $.getScript('assets/demo/default/custom/components/forms/widgets/select2.js',function(){
		   });
		  
		   
		   
//		   this._script.load('.m-grid__item.m-grid__item--fluid.m-wrapper',
//'assets/demo/default/custom/components/forms/widgets/select2.js');
//		   this._script.load('.m-grid__item.m-grid__item--fluid.m-wrapper',
//'assets/app/js/bootstrap-material-datetimepicker.js');
	}
  toggleAnimation(){
		   this.router.navigate(['helpdesk/ticket-list']);
	  }
 
populateDropDownData(){
			  const headers = new HttpHeaders({'Content-Type':'application/json'});
			  this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/branch',{},
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.customers = data['populateBranch']
			  															});
	          this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/project-type',{},
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.types = data['populateProjectType']
			  															});
              this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/project-status',{},
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.Status = data['populateProjectStatus']
			  															});   
	          this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/teamexecutives',{},
			    					{ headers, withCredentials: true }).subscribe(data =>{
				  														this.Executives = data['populateExecutive']
			  															});   
}
  populateData(){
	  const headers = new HttpHeaders({'Content-Type':'application/json'});
	  this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/pm/pm',
									JSON.stringify({ projectId :this.projectId}),
						{ headers, withCredentials: true }).subscribe(data => {
		                                  this.project = data['populateData'][0];
			  															},
															 (err) => {
																	console.log("ERROR: "+err);
															 },
															 () => {
			  												this.populateDropDownData();
				                                              });
 }
 

getCheckValues(temp:string){
	return this.globalValid.getCheckValue(temp);
	
	}
onSubmit() {
		this.bootstrapFormValues = $('#m_form_1').serializeArray();
		for(var i=0;i<this.bootstrapFormValues.length;i++){
			 if(this.bootstrapFormValues[i].name.includes('check_')){
					this.project[this.bootstrapFormValues[i].name.split('_')[1]] = "1";
				}else{
					this.project[this.bootstrapFormValues[i].name] = this.bootstrapFormValues[i].value;
				}
		}
	console.log(this.project);
	if(!this.projectForm.invalid ){
			   const headers = new HttpHeaders({'Content-Type':'application/json'});
		if(this.projectId == 0){
			 this.project['requestType'] = 'add';
//			this.customer['tag'] = 'customer';
		}else{
			this.project['requestType'] = 'update';
			this.project['projectId'] = this.projectId+'';
		}
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/pm/pm',
									JSON.stringify(this.project),
						{ headers, withCredentials: true }).subscribe(data => {
			 											if(data['successmsg'] == undefined){
														this.msg[0]='Error';
			 											this.msg[1]=data['errormsg'];
														}else{
														if(data['successmsg'].includes('Successfully')){
														this.msg[0]='success';
			 											this.msg[1]=data['successmsg'];
														}
														}
			  											this.toast.showtoast(this.msg);
			  											if(this.msg[0]!='Error'){
														this.toggleAnimation();
			 											this.projectForm.reset();
														}
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
		 }
			
			
			
}	
}

export class ProjectData {
	
	pmTitle:string;
    pmBudget:string;
    customerId:string;
    pmPmtypeId:string;
    pmStartDate: string='';
    pmEndDate: string='';
    pmPmstatusId:string;
    pmexeEmpIds:string;
	pmComplete:string="0";
	pmActive:string="1";
    pmComment:string;
    pmNotes:string;
}


